export default class Frame {
  constructor(img) {
    this.img = img;
  }

  getImage() {
    return this.img;
  }

  setImage(image) {
    this.img = image;
  }

  static fromJS(dataImage) {
    const newImg = document.createElement('img');
    newImg.src = `data:image/png;base64,${dataImage.img}`;
    return new Frame(newImg);
  }

  toJS() {
    const imgData = Frame.getBase64Image(this.img);
    return {
      img: imgData,
    };
  }

  static getBase64Image(img) {
    const canvas = document.createElement('canvas');
    canvas.width = 500;
    canvas.height = 500;

    if (canvas.getContext) {
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0);
    }
    const dataURL = canvas.toDataURL('image/png');
    return dataURL.replace(/^data:image\/(png|jpg);base64,/, '');
  }

  // getSize() {
  //   return this.size;
  // }

  // setSize(size) {
  //   this.size = size;
  // }
}
