import GIF from './gif';
import download from './download';

export default class ExportAnimation {
  constructor(fps) {
    this.framerate = Math.round(1000 / fps);
    this.gif = null;
  }

  render(frames) {
    this.gif = new GIF({
      workers: 2,
      workerScript: './workerGif.bundle.js',
      quality: 10,
      debug: true,
      // width: size.width,
      // height: size.height,
    });
    frames.forEach((imageElement) => {
      const image = imageElement.getImage();
      this.gif.addFrame(image, { delay: this.framerate });
    });
    // frames.forEach((imageElement) => {
    //   const image = imageElement.getImage();
    //   const canvas = document.createElement('canvas');
    //   canvas.width = size.width;
    //   canvas.height = size.height;
    //   const img = new Image();
    //   let dataURL = '';
    //   if (canvas.getContext) {
    //     const ctx = canvas.getContext('2d');
    //     img.onload = () => {
    //       ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
    //       dataURL = canvas.toDataURL('image/png');
    //     };
    //     img.src = dataURL;
    //   }

    //   this.gif.addFrame(img, { delay: this.framerate });
    // });

    this.gif.on('finished', (blobUrl) => {
      window.open(URL.createObjectURL(blobUrl));
      download(blobUrl, 'dlDataUrlBin.gif', 'image/gif');
    });
    this.gif.render();
  }
}
