import Frame from '../frames-list/Frame';

export default class AppModel {
  constructor() {
    this.activeFrame = 0; // number
    this.frames = [];
    this.width = 32;
    this.height = 32;
  }

  createFrame(image) {
    const frame = new Frame(image);// { width: size, height: size });
    this.frames.push(frame);
  }

  updateFrame(index, image) {
    this.getFrame(index).setImage(image);
  }

  removeFrame(index) {
    if (index !== -1) {
      this.frames.splice(index, 1);
    }
  }

  getFrame(index) {
    return this.frames[index];
  }

  insertFrame(index) {
    const image = this.getFrame(index).getImage();
    this.frames.splice(index, 0, new Frame(image));
  }

  getLength() {
    return this.frames.length;
  }

  setSize(size) {
    this.width = size;
    this.height = size;
  }

  getSize() {
    return { width: this.width, height: this.height };
  }

  fromJS(js) {
    this.activeFrame = js.activeFrame;
    this.frames = js.frames.map(frame => Frame.fromJS(frame));
    this.width = js.width;
    this.height = js.height;
  }

  toJS() {
    return {
      activeFrame: this.activeFrame,
      frames: this.frames.map(frame => frame.toJS()),
      width: this.width,
      height: this.height,
    };
  }

  saveModel() {
    localStorage.setItem('model', JSON.stringify(this.toJS()));
  }

  static loadModel() {
    const model = localStorage.getItem('model');
    return model;
  }
}
